:root {
  --background-color: #11111b;
  --backdrop-color: rgb(12, 10, 17);
  --txt-light-color: #ecd8bf;
  --txt-color: #ded6fc;
  --txt-dim-color: rgba(183, 172, 226, 0.9);
  --txt-dimmer-color: rgba(145, 138, 211, 0.8);
  --main-color: #ffab73;
  --banner-shadow-color: #ff9f7357;
  --box-shadow-color: rgba(145, 138, 211, 0.1);
  --shadow-color: rgba(5, 0, 25, 0.5);
  --website-width: 1024px;
  --background-gradient: linear-gradient(45deg, rgba(14,13,25,1) 0%, rgba(36,30,47,1) 100%);
}

h1, span, p, a, li, ul, ol, h2, h3, h4, h5, h6 {
  transition: all 200ms ease-in-out;
}

.disabled {
  opacity: 0.5;
  pointer-events: none;
}

.light {
  font-weight: 400;
  font-size: 1rem;
  letter-spacing: +0.02rem;
  color: var(--txt-dim-color);
}

.date {
  width: 100%;
  text-align: left;
  margin: 0;
  margin-bottom: 3rem;
  padding: 0;
}

body {
  background-color: var(--backdrop-color);
}

.App {
  background-color: var(--backdrop-color);
  color: var(--txt-color);
  text-align: center;
  font-family: 'Poppins', sans-serif;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column
}

.App_Container {
  background-color: var(--backdrop-color);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: var(--website-width);
  width: 100%;
}

.App_Banner {
  border-radius: 2rem;
  height: 100%;
  transition: all 200ms ease-in-out;
  background-color: var(--backdrop-color);
}

.App_Box:nth-child(1) {
  margin-top: 1.5rem;
  background-color: var(--backdrop-color);
}

.App_Box:nth-child(2) {
  position: relative;
  padding-left: 2rem;
  padding-right: 2rem;
  padding-bottom: 2rem;
}

.App_Box:last-child {
  margin-bottom: 2rem;
}

.App_Box {
  border-radius: 2rem;
  background-color: var(--background-color);
  margin-top: 1rem;
  background: var(--background-gradient);
  background-size: 400% 400%;
  animation: app_box_bg 15s ease-in-out infinite;
  transition: all 200ms ease-in-out;
  max-width: var(--website-width);
}

hr {
  width: 100%;
  max-width: var(--website-width);
  border: 1px solid rgba(4, 4, 8, 0.2);
}

.App_Article {
  text-align: left;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.App_Aktualnosci {
  font-size: 4rem;
  margin: 2rem 0;
}

.App_Article h2 {
  width: 100%;
  text-align: left;
  margin: 0;
  padding: 0;
  margin-top: 2rem;
}

.App_Article span {
  color: var(--txt-dim-color);
}

.App_Box hr {
  margin-top: 2.5rem;
}

@media screen and (max-width: 1024px) {
  .App_Box {
    border-radius: 0;
    margin: 0;
  }

  .App_Box:nth-child(1) {
    margin-top: 0;
  }

  .App_Box:last-child {
    margin-bottom: 0;
  }

  .App_Banner {
    border-radius: 0;
  }

  .App_Aktualnosci {
    font-size: 3rem;
  }
}

.App_Article ul li {
  color: var(--txt-dimmer-color);
}

.App_Footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: rgba(183, 172, 226, 0.2);
  padding: 0.5rem 1rem;
  border-radius: 1rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  font-size: 1rem;
}

/* ANIMACJE */

@keyframes app_box_bg {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.App_Article_Box {
  margin: 0;
  padding: 2rem;
  border-radius: 1.5rem;
  background-color: rgba(11, 10, 19, 0.445);
  display: flex;
  flex-direction: column;
}