@import "~react-image-gallery/styles/css/image-gallery.css";

.Galeria {
    width: 100%;
    height: 360px;
    margin: 0 auto;
    padding: 0;
    color: #fff;
    font-family: "Roboto", sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
    text-align: center;
    margin-top: 1rem;
}

.image-gallery-content.bottom .image-gallery-slide .image-gallery-image, .image-gallery-slide .image-gallery-image {
    max-height: 80vh !important;
}