.Navbar {
  background-color: var(--background-color);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  color: var(--txt-color);
  position: relative;
  z-index: 100;
  font-family: 'Poppins', sans-serif;
}

.Navbar_Container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  height: 4rem;
  width: 100%;
  max-width: var(--website-width);
}

.Navbar_Menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  margin-right: 1rem;
}

.Navbar_Item {
  margin-left: 0.5rem;
  transition: all 100ms ease-in-out;
}

.Navbar span {
  background-color: var(--backdrop-color);
  color: var(--txt-color);
  padding: 4px 16px;
  border-radius: 0.5rem;
  font-weight: 600;
  font-size: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.Navbar_Item:hover {
  cursor: pointer;
  color: var(--background-dim-color);
}

.Navbar_Logo {
  margin-left: 1rem;
  margin-top: 0.5rem;
  transition: all 100ms ease-in-out;
}

.Navbar_Logo:hover {
  cursor: pointer;
  transform: translateX(4px);
}

.Navbar_Logo:active {
  transform: translateX(0px);
}

.Navbar span b {
  font-weight: 600;
  font-size: 0.9rem;
}

.Navbar span:hover hr {
  border: 2px solid var(--main-color);
}

.Navbar span:hover {
  cursor: pointer;
  color: var(--main-color);
  transform: translateY(-4px);
  filter: drop-shadow(0 1rem 1rem) var(--shadow-color);
}

.Navbar span:active {
  transform: translateY(0px);
}